import { Dispatch, FC, SetStateAction } from "react"
import { Quiz } from "./Quiz"
import { createQuizData } from "./QuizData"
import { QuizMode, QuizModes } from "./QuizMode"

type SelectorProps = {
    setQuiz: Dispatch<SetStateAction<Quiz>>,
    current: Quiz,
}

type ModeProps = {
    setQuiz: Dispatch<SetStateAction<Quiz>>,
    mode: QuizMode,
    selected: boolean,
}

export const ModeLabel: FC<ModeProps> = ({
    setQuiz,
    mode,
    selected,
}) => {
    const { label, style } = QuizModes[mode]
    return (
        <span className={`quiz_mode quiz_mode_${selected ? "selected" : mode} ${style}`}
            style={selected ? {"cursor": "not-allowed"} : {}}
            onClick={() => {
                if (!selected && window.confirm(`„Äå${label}„Äç„É¢„Éº„Éâ„Å´\n„Åã„Åà„Åæ„Åô„ÅãÔºü`)) {
                    const data = createQuizData(mode)
                    setQuiz({
                        mode: mode,
                        data: data,
                    })
                }
            }}
        >
            {label}
        </span>
    )
}

export const QuizSelector: FC<SelectorProps> = ({
    setQuiz,
    current,
}) => {
    return (
        <>
            <h5 style={{textAlign: 'center'}}>
                {Object.keys(QuizModes).map(mode => 
                    <ModeLabel key={mode}
                        setQuiz={setQuiz}
                        mode={mode as QuizMode} 
                        selected={current.mode === mode}
                    />
                )}
            </h5>
        </>
    )
}